<template>
 <div class="data-picker__container">
    <div class="data-picker__checkBtn">
        <el-radio-group v-model="radio">
        <el-radio :label="3">One way</el-radio>
        <el-radio :label="6">Return</el-radio>
        <el-radio :label="9">Multy-city C</el-radio>
    </el-radio-group>
    </div>
   <div class="data-picker__wrapper">
        <div class="data-picker__date">
        <el-date-picker
        v-model="value1"
        type="date"
        placeholder="Pick a day">
        </el-date-picker>
    </div>
  <div class="data-picker__date">
    <el-date-picker
      v-model="value2"
      type="date"
      placeholder="Pick a day"
      :picker-options="pickerOptions">
    </el-date-picker>
  </div>
      <el-button class="top-section__btn" round>Sign up</el-button>

</div>
 </div>
</template>

<script>
  export default {
    data() {
      return {
        radio: 3,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: 'Today',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: 'Yesterday',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: 'A week ago',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
      };
    }
  };
</script>

<style>
.data-picker__container {
    z-index: 12;
    max-width: 1240px;
    width: 100%;
    padding-top: 10px;
    padding-bottom: 5px;
    background: #fff;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
}
.data-picker__checkBtn {
    padding-top: 33px;
    margin: 0 auto;
}
.data-picker__wrapper {
    display: flex;
    justify-content: center;
    margin: 31px;
}
@media (max-width: 800px) {
    .data-picker__container {
       width: 90%;
       margin: 0 auto;
    }
    .data-picker__wrapper {
        display: flex;
        flex-direction: column;
        width: 90%;
    }
}
</style>